import React, { Component, Ref } from 'react';
import styled from 'styled-components';
import classNames from 'classnames';
import { card_source, PlayerType, card_width, card_height } from '../constants';
import { delay } from '../utils';
import CardDesc from './card-info';
import RoughBox from './rough-box';

const Wrapper = styled.div`
  width: ${card_width}px;
  height: ${card_height}px;
  text-align: center;
  font-size: 12px;
  user-select: none;
  cursor: move;
  position: relative;
  opacity: 0.5;

  &.can-use {
    opacity: 1;
  }

  .card-border {
    /* svg 的尺寸要设置的略大于父容器 */
    width: 120%;
    height: 120%;
    position: absolute;
    top: 0;
    left: 0;
  }

  &.is-dragging {
    opacity: 0.3;
  }

  p {
    margin: 0;
  }

  .name {
    font-size: 16px;
    margin-bottom: 16%;
    position: relative;
    top: 12px;
  }

  .desc.emoji {
    font-size: 30px;
  }
`;

interface ICardViewProps {
  playCard: () => any;
  source?: PlayerType;
  isDragging?: boolean;
  name: string;
  desc: string;
  attack?: number;
  armor?: number;
  mana: number;
  extraInfo?: string;
  usable?: boolean;
}

interface IState {
  shouldShowInfo: boolean;
}

class CardView extends Component<ICardViewProps> {
  isMouseIn: boolean;
  state: IState;
  cardBorder: SVGSVGElement;

  constructor(props: ICardViewProps) {
    super(props);
    this.state = {
      shouldShowInfo: false,
    };
    this.isMouseIn = false;
  }
  
  componentDidMount() {
    // this.setBorder();
  }
  

  handleDoubleClick = () => {
    this.props.playCard();
  }

  // setBorder = () => {
  //   const svg: SVGSVGElement = this.cardBorder;

  //   const rc = rough.svg(svg);
  //   const strokeColor = this.props.source === card_source.monster
  //     ? 'red'
  //     : 'black';

  //   const node = rc.rectangle(0, 0, 100, 130, {
  //     stroke: strokeColor
  //   });
  //   (svg as any).appendChild(node);
  // }

  handleHover = async () => {
    this.isMouseIn = true;

    // 延时之后显示额外的卡牌说明信息
    await delay(1000);
    if (!this.isMouseIn || this.props.isDragging) { return; }
    this.setState({ shouldShowInfo: true });
  }

  handleLeave = () => {
    this.setState({ shouldShowInfo: false });
    this.isMouseIn = false;
  }

  setSvgRef = (element:SVGSVGElement) => {
    this.cardBorder = element;
  };

  render() {
    const { name, desc, attack, armor, source, isDragging, extraInfo, mana, 
      usable=true } 
      = this.props;

    const strokeColor = source === card_source.monster ? 'red' : 'black';

    return (
      <Wrapper 
        className={classNames('card', source, { 'is-dragging': isDragging, 'can-use': usable})}  
        onDoubleClick={this.handleDoubleClick}
        onMouseEnter={this.handleHover}
        onMouseLeave={this.handleLeave}
      >
        {/* <svg className="card-border" ref={this.setSvgRef} /> */}
        <RoughBox width={card_width} height={card_height} strokeColor={strokeColor} />
        <p className="name">{name}</p>
        <p className={desc.length <=2 ? 'desc emoji' : 'desc'}>{desc}</p>
        {attack as number > 0 && <p>攻击：{attack}</p>}
        {armor as number > 0  && <p>护甲：{armor}</p>}
        <p>法力：{mana}</p>

        {(this.state.shouldShowInfo && !isDragging) && <CardDesc info={extraInfo} />}
      </Wrapper>
    )
  }
}

export default CardView;



